* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.root-grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns, each 1 fraction of the available width */
  grid-template-rows: repeat(2, 1fr);    /* 2 rows, each 1 fraction of the available height */
  height: 100vh;                         /* Full viewport height */
}

.grid-item {
  border: 1px solid #dfdfdf;                /* Optional: for visual separation */
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.chart-wrapper {
  padding: 10px;
  
  .echarts-for-react {
    width: 100%;
    height: 100%;
  }
}

.home-container {
  display: flex;
  width: 100%;
  height: 100vh; /* Full screen height */
  padding: 5px;

  .left-column,
  .right-column {
    width: 50%; /* Equal width for both columns */
    display: flex;
    flex-direction: column;
    gap: 10px; /* Add space between cells if desired */
    padding: 5px;
  }

  .cell {
    /* background-color: #e0e0e0; Light gray for visualization */
    border: 1px solid #ccc;
    flex: 1; /* Makes cells stretch evenly */
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

